@import "../../global.scss";

.navbar {
    width: 100%;
    height: 70px;
    background-color: $secondaryColor;
    color: $primaryColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
            }
            
            a {
                color: $primaryColor;

                &:hover {
                    color: $tertiaryColor;
                }
            }
        }
        .itemContainer {
            display: flex;
            align-items: center;

            @include mobile {
                display: none;
            }

            .icon {
                font-size: 35px;
                padding: 0 10px;
            }

            a {
                color: $primaryColor;

                &:hover {
                    color: $tertiaryColor;
                }
            }
        }

        .right {
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span {
                    width: 100%;
                    height: 3px;
                    background-color: $primaryColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active {
        background-color: $primaryColor;
        color: white;

        .hamburger {
            span {
                &:first-child {
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}
